<template>
  <div id="app">
    <Operation></Operation>
    <input class="title" type="text" v-show="content_title !== ''" v-model="title" />
    <textarea class="full_content" v-show="when_content !== ''" v-model="content"></textarea>
  </div>
</template>

<script>
import Operation from './components/operation'

export default {
/* eslint-disable */
  name: 'App',
  data () {
    return {
      content: '',
      id: '',
      title: ''
    }
  },
  watch: {
    content: {
      handler () {
        this.$store.commit('change_content', {
          id: this.id,
          content: this.content
        })
      },
      deep: true
    },
    title: {
      handler () {
       this.$store.commit('change_title', {
         id: this.id,
         title: this.title
       })
      },
      deep: true
    }
  },
  computed: {
    choose_index () {
      return this.$store.state.choose_index
    },
    when_content () {
      this.content = this.$store.state.when_content
      this.id = this.$store.state.choose_id
      return this.$store.state.when_content
    },
    content_title () {
      this.title = this.$store.state.title
      return this.$store.state.title
    }
  },
  components: {
    Operation
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
  position: relative;
}
</style>



